import { useCallback, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Grid, TextField, Card, CardHeader, CardContent, InputAdornment } from '@mui/material';
import { useAppStore } from '../../../store';
import { AppButton, AppIconButton, AppLink } from '../../../components';
import { AppForm, AppAlert } from '../../../components/forms';
import { useAppForm, SHARED_CONTROL_PROPS, eventPreventDefault } from '../../../utils/form';
import { api } from '../../../api';


const VALIDATE_FORM_LOGIN_EMAIL = {
  email: {
    presence: true,
    email: true,
  },
  code: {
    presence: true,
  },
  password: {
    presence: true,
    length: {
      minimum: 8,
      maximum: 32,
      message: 'must be between 8 and 32 characters',
    },
  },
};

/**
 * Renders "Login with Email" view for Login flow
 * url: /auth/login/email/*
 */
const LoginEmailView = () => {
  const navigate = useNavigate();
  const [, dispatch] = useAppStore();
  const [formState, , /* setFormState */ onFieldChange, fieldGetError, fieldHasError] = useAppForm({
    validationSchema: VALIDATE_FORM_LOGIN_EMAIL,
    initialValues: { email: '', code: '', password: '' },
  });
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState();
  const [showCode, setShowCode] = useState(false);
  const values = formState.values; // Typed alias to formState.values as the "Source of Truth"

  const handleShowPasswordClick = useCallback(() => {
    setShowPassword((oldValue) => !oldValue);
  }, []);
  const handleFormSubmit = useCallback(
    async (event) => {
      event.preventDefault();

      const result = await api?.auth?.login(values);
      if (!result) {
        setError('Please check sms code again');
        return; // Unsuccessful login
      }

      dispatch({ type: 'LOG_IN' });
      navigate('/', { replace: true });
    },
    [dispatch, values, navigate]
  );

  const handleLogin = async () => {
    const result = await api?.auth?.post("api/auth", values);
    if (result !== "Sent Code") {
      setError('Please check email and password');
      return; // Unsuccessful login
    }
    setShowCode(true);
  }

  const handleCloseError = useCallback(() => setError(undefined), []);

  return (
    <AppForm onSubmit={handleFormSubmit}>
      <Card sx={{ minWidth: 275, maxWidth: 375 }}>
        <CardHeader title="Login into your account" style={{ textAlign: 'center' }} />
        <CardContent>
          <TextField
            required
            label="Email"
            name="email"
            value={values.email}
            error={fieldHasError('email')}
            helperText={fieldGetError('email') || ' '}
            onChange={onFieldChange}
            {...SHARED_CONTROL_PROPS}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AppIconButton
                    aria-label="Email"
                    icon={'email'}
                    title={''}
                  />
                </InputAdornment>
              ),
            }}
          />

          <TextField
            required
            type={showPassword ? 'text' : 'password'}
            label="Password"
            name="password"
            value={values.password}
            error={fieldHasError('password')}
            helperText={fieldGetError('password') || ' '}
            onChange={onFieldChange}
            {...SHARED_CONTROL_PROPS}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AppIconButton
                    aria-label="Password"
                    icon={'smartphone'}
                    title={''}
                  />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end">
                  <AppIconButton
                    aria-label="toggle password visibility"
                    icon={showPassword ? 'visibilityon' : 'visibilityoff'}
                    title={showPassword ? 'Hide Password' : 'Show Password'}
                    onClick={handleShowPasswordClick}
                    onMouseDown={eventPreventDefault}
                  />
                </InputAdornment>
              )
            }}
          />

          {showCode && <TextField
            required
            label="Enter your code"
            name="code"
            value={values.code}
            error={fieldHasError('code')}
            helperText={fieldGetError('code') || ' '}
            onChange={onFieldChange}
            {...SHARED_CONTROL_PROPS}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AppIconButton
                    aria-label="code"
                    icon={'key'}
                    title={''}
                  />
                </InputAdornment>
              ),
            }}
          />
          }

          {error ? (
            <AppAlert severity="error" onClose={handleCloseError}>
              {error}
            </AppAlert>
          ) : null}
          <Grid container justifyContent="center" alignItems="center">
            {!showCode && <AppButton type="button" disabled={!formState.isValid} onClick={handleLogin}>
              Log in
            </AppButton>
            }
            {showCode && <AppButton type="submit">
              Enter your Code
            </AppButton>
            }
          </Grid>
        </CardContent>
      </Card>
    </AppForm>
  );
};

export default LoginEmailView;
