import React, { useState, useEffect, useRef, PropsWithChildren } from "react";
import socketIOClient, { Socket, io } from "socket.io-client";

export const SocketContext = React.createContext(
  {}
);

export const SocketContextProvider = ({
  children,
}) => {
  const [client, setClient] = useState(null);
  const [twilioToken, setTwilioToken] = useState(null);

  useEffect(() => {
    // const socket = socketIOClient(process.env.REACT_APP_API_URL);
    // setCurSocket(socket);
    // const initial = io.connect(process.env.REACT_APP_API_URL);
    // setClient(initial);
  }, []);

  const addToken = (token) => {
    console.log("socket : add token");
    const query_client = io.connect(process.env.REACT_APP_API_URL, { query: { token } });
    setClient(query_client);
  }
  const removeToken = () => {
    console.log("socket : remove token");
    const remove_client = io.connect(process.env.REACT_APP_API_URL);
    setClient(remove_client);
  }

  return (
    <SocketContext.Provider
      value={{
        curSocket: client,
        twilioToken,
        addToken,
        removeToken,
        setTwilioToken
      }}
    >
      {children}
    </SocketContext.Provider>
  );
};
