import { useContext } from "react";
import { SocketContext } from "../contexts/SocketContext";

const useSocket = () => {
  const { curSocket, addToken, removeToken, twilioToken, setTwilioToken } = useContext(SocketContext);
  return {
    curSocket,
    twilioToken,
    addToken,
    removeToken,
    setTwilioToken
  };
};

export default useSocket;
