import * as React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import Avatar from '@mui/material/Avatar';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import CallIcon from '@material-ui/icons/Call';
import CallEndIcon from '@material-ui/icons/CallEnd';
import Stack from '@mui/material/Stack';
import Fab from '@mui/material/Fab';
import avatar_image from '../../assets/call_avatar.png';
import useCall from '../../hooks/useCall';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));

export default function InboundDialog() {

  const { call, setIsCallingState, isIncomingCall, setIsIncomingCall, acceptIncomingCall,
    rejectIncomingCall,
    hangupIncomingCall,
    handleDisconnectedIncomingCall,
    setIsCalling } = useCall();
  const handleClickOpen = () => {
    setIsIncomingCall(true);
  };
  const handleClose = () => {
    try {
      rejectIncomingCall(call);
    } catch (error) {
      console.log(error);
    }
    setIsIncomingCall(false);
  };

  const handleAccept = () => {
    acceptIncomingCall(call);
    setIsIncomingCall(false);
    setIsCalling(true);
    setIsCallingState(true)
  }

  return (
    <div>
      <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={isIncomingCall}
      >
        <DialogContent>
          <>
            <Stack spacing={3} justifyContent="center" direction="row" sx={{ minWidth: 180 }}>
              <Avatar src={avatar_image} sx={{ width: 84, height: 84 }} />
            </Stack>
            <Stack spacing={3} justifyContent="center" direction="row">
              <Typography variant="h6">
                Client
              </Typography>
            </Stack>
            <br />
            <Stack spacing={3} justifyContent="center" direction="row">
              <Typography variant="text">
                Incoming call
              </Typography>
            </Stack>
            <Stack spacing={3} justifyContent="center" direction="row">
              <Typography variant="text">
                {call?.parameters?.From}
              </Typography>
            </Stack>
            <br />
            <Stack spacing={3} justifyContent="center" direction="row">
              <Fab aria-label="call-accept" color="primary" sx={{ width: 40, height: 40 }} onClick={() => handleAccept()}>
                <CallIcon sx={{ width: 30, height: 30 }} />
              </Fab>
              <Fab aria-label="call-end" color="error" sx={{ width: 40, height: 40 }} onClick={handleClose}>
                <CallEndIcon sx={{ width: 30, height: 30 }} />
              </Fab>
            </Stack>
          </>
        </DialogContent>
      </BootstrapDialog>
    </div>
  );
}