import Grow from '@mui/material/Grow';
import Paper from '@mui/material/Paper';
import Popper from '@mui/material/Popper';
import MenuItem from '@mui/material/MenuItem';
import MenuList from '@mui/material/MenuList';
import Stack from '@mui/material/Stack';
import MenuButton from './MenuButton';

const menuList = [
  { main: "Profile", content: [], router: ["http://test.medicaremagician.us/#/general/home"] },
  { main: "Address Book", content: ["View Agents", "View Customers", "View Prospects"], router: ["users/agents", "users/customers", "users/prospects"] },
  // { main: "Calendar", content: ["View Daily", "View Weekly", "View Monthly", "View Yearly"], router: ["", "", "", ""] },
  { main: "Call Center", content: ["Phone Call", "In/Out"], router: ["call", "", ""] },
  { main: "Log Out", content: [], router: ["http://test.medicaremagician.us/"] },
  // { main: "Folders", content: ["View", "Add New"], router: ["", "", "", ""] },
  // { main: "Mail", content: ["View Message", "New SMS", "View Inbox"], router: ["", "", "", ""] },
  // { main: "Sales", content: ["View Opporunities", "New Opportunity"], router: ["", "", "", ""] },
  // { main: "Services", content: [] },
  // { main: "Tasks", content: ["View Tasks", "New Task"], router: ["", "", "", ""] },
  // { main: "Search", content: [] },
]

export default function MenuListComposition({ className }) {
  const [open, setOpen] = React.useState(false);
  const [curMenu, setCurMenu] = useState(0);
  const anchorRef = React.useRef(null);

  const handleToggle = (index) => {
    setOpen((prevOpen) => !prevOpen);
    setCurMenu(index);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpen(false);
    } else if (event.key === 'Escape') {
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = open;
  }, [open]);

  return (
    <Stack direction="row" spacing={2} className={className}>
      {
        menuList.map((item, i) => (
          <MenuButton main={item.main} content={item.content} routers={item.router} />
        )
        )
      }
    </Stack>
  );
}